/* global d3 */
import debounce from 'lodash.debounce';
import isMobile from './utils/is-mobile';
import graphic from './graphic';
import footer from './footer';

const $body = d3.select('body');
let previousWidth = 0;


function setupStickyHeader() {
  const $header = $body.select('header');
  if ($header.classed('is-sticky')) {
    const $menu = $body.select('.header__menu');
    const $toggle = $body.select('.header__toggle');
    $toggle.on('click', () => {
      const visible = $menu.classed('is-visible');
      $menu.classed('is-visible', !visible);
      $toggle.classed('is-visible', !visible);
    });
  }
}

function init() {
  // add mobile class to body tag
  $body.classed('is-mobile', isMobile.any());
  // setup resize event

  // setup sticky header menu
  setupStickyHeader();
  // kick off graphic code
  Promise.all([
    // DEV
    d3.json("./assets/data/Route248.geojson"),
    d3.json("./assets/data/Northampton.geojson"),
    d3.csv("./assets/data/Route248_data.csv"),
    d3.json("./assets/data/markerData.json")
  ]).then(function (files) {
    const routeData = files[0];
    const townData = files[1];
    const townInfo = files[2];
    const markerData = files[3];
    graphic.init({ routeData, townData, townInfo, markerData });

    window.addEventListener('resize', debounce(function resize() {
      const width = $body.node().offsetWidth;
      if (previousWidth !== width) {
        previousWidth = width;
        graphic.resize({ routeData, townData, townInfo, markerData });
      }
    }, 250));

  });

  // load footer stories
  footer.init();
}

init();
