import isMobile from './utils/is-mobile';

export default function lazyLoadImg() {
    const is_mobile = isMobile.any();

    const allElem = ['.article__graphic', '.article__img', '.article__locator'];
    allElem.forEach(d => {
        if (!is_mobile && ['.article__graphic', '.article__locator'].indexOf(d) > -1) {
            // desktop 
            return;
        } else {
            const allNodes = d3.selectAll(`img${d}`).nodes();
            allNodes.forEach(node => {
                const pic = d3.select(node);
                const dataSrc = pic.attr('data-src')
                pic.attr('src', dataSrc);
            })
        }
    })
}