import getSize from './getSize';
import isMobile from './utils/is-mobile';
import slugify from './utils/slugify.js';

function Map(data) {
    let [W, H] = getSize('map');
    const $svg = d3.select('#map__svg');
    const { routeData, townData, townInfo, markerData } = data;
    const _red = '#D30E14', _purple = "#95519e", _blue = '#307FE2', _green = '#BAD131', _green2 = "#006d18";
    const is_mobile = isMobile.any();
    let _transition = `translate(${is_mobile ? -W * .7 : 0}, 0)scale(1)`;
    
    function init() {
        [W, H] = getSize('map');
        $svg.attr('width', W)
            .attr('height', H)
        const $plotG = $svg.selectAll('.plotG')
            .data([1])
            .join('g')
            .attr('class', 'plotG')
            .attr('transform', `translate(0, 0)`)

        $plotG.selectAll('.layerG')
            .data(['countyG', 'routeG', 'labelG', 'markerG', 'noteG'])
            .join('g')
            .attr('class', d => `layerG ${d}`)

        const $countyG = $plotG.select('.countyG').attr('transform', 'translate(0, 20)');
        const $routeG = $plotG.select('.routeG').attr('transform', 'translate(0, 20)');
        // const $legendG = $plotG.select('.legendG');
        const $noteG = $plotG.select('.noteG').classed('active', true)
        // const $annotationG = $plotG.select('.annotationG')
        //     .attr('transform', `translate(${W / 3}, 50)`)
        //     .classed('active', true);
        // const projection = d3.geoMercator()
        //     .scale([mapScale])
        //     .translate([W/2, H/2])
            // .center(centre_county);
        const projection = d3.geoTransverseMercator()
        .rotate([76 + 30 / 60, -38 - 50 / 60])
        .fitExtent([[60, 10], [W-60, Math.min(W-70, H*.7-40) ]], townData)
        const path = d3.geoPath()
            .projection(projection);


        const startPoint = projection([-75.698890972731462, 40.829051151673362])
   
        $countyG.selectAll('.ctyPath')
            .data(townData.features)
            .join("path")
            .attr('d', path)
            .attr('class', 'ctyPath')
            .attr('id', d => slugify(d.properties['Northampton Precinct Vote_pct_town_name']))
            .attr("fill", d => fillFunc(d))
            .attr("stroke-width", 1)
            .attr("stroke", "#e2e3e4")

        $routeG.selectAll('.routePath')
            .data(routeData.features)
            .join("path")
            .attr('d', path)
            .attr("fill", 'none')
            .attr("stroke-width", 5)
            .attr("stroke", "#333333")

        $noteG.selectAll('.note')
        .data(['Route 248'])
        .join('text')
        .attr('class', 'note')
        .text(d=> d)
        .attr('x', startPoint[0]+20)
        .attr('y', startPoint[1]+20)
        .attr('fill', '#333333')
        .attr('font-weight', 800)

    }

    init.circleLoop = function (_loop) {
        if (_loop) {
            $svg.select('.markerG')
                .selectAll('.marker')
                .attr('r', 1)
                .transition()
                .duration(1000)
                .attr('r', 5)
                .transition()
                .delay(1000)
                .attr('r', 1)
            // .each("end", this.circleLoop(true));
        }
    }

    init.highlightTown = function (arr) {
        const $countyG = $svg.select('.countyG');
        if (!arr.length) {
            $countyG
                .selectAll('.ctyPath')
                .attr('stroke-width', 1)
                .attr('stroke', 'white')
                .attr('opacity', 1)
                .attr('fill-opacity', 1)
                return;
        }
        
        $countyG
            .selectAll('.ctyPath')
            .attr('stroke-width', 1)
            .attr('stroke', 'white')
            .attr('opacity', 0.1 )
            // .attr('fill-opacity', is_mobile? 0.1: 0.3)

        arr.forEach( (d,i) => {
            $countyG.select(`#${slugify(d)}`)
                .attr('stroke-width', 4)
                .attr('opacity', 1 )
                .attr('stroke', _green )
                .attr('fill-opacity', '1')
                .raise();
        })
        // $countyG.select(`#${}`)
        //     .selectAll('.ctyPath')
        //     .attr("stroke-width", d=>{
        //         console.log(d.properties['Northampton Precinct Vote_pct_town_name'])
        //         return arr.indexOf(d.properties['Northampton Precinct Vote_pct_town_name'])>-1? 5: 1
        //     })
        //     .attr("stroke", d=>{
        //         return arr.indexOf(d.properties['Northampton Precinct Vote_pct_town_name'])>-1? 'gold': 'white'
        //     })
    }
    init.transitMap = function (_transition) {
        W = getSize('map')[0];
        $svg.select('.plotG')
            .transition()
            .duration(750)
            .attr("transform", _transition)
            .style("stroke-width", 1.5 + "px");
    }

    function fillFunc(d) {
        const attr = d.properties['Northampton Precinct Vote_pct_pct'];
        const fillColor = d3.scaleLinear()
            .domain([-0.4, 0.4])
            .range([_blue, _red]);
        return fillColor(attr)
    }

    function parseChapterData(data) {
        const arr = [];
        for (const attr in data) {
            if (attr != 'overview') {
                arr.push({ 'attr': attr, 'pos': data[attr].pos.center })
            }
        }
        return arr;
    }

    return init
}

export default Map;