import getSize from './getSize';
import isMobile from './utils/is-mobile';
import slugify from './utils/slugify';

function ScatterPlot(townInfo) {
    let [W, H] = getSize('map');
    const $svg = d3.select('#map__svg');
    const _red = '#D30E14', _green = "#BAD131", _green2 = "#006d18", _purple = "#95519e", _blue = '#307FE2', _yellow = '#BAD131', _gray = '#888888';
    const is_mobile = isMobile.any();

    function init() {
        [W, H] = getSize('map');
        const rangeW = 160;
        const $scatterG = $svg.selectAll('.scatterG')
            .data([1])
            .join('g')
            .attr('class', `scatterG`)
            .attr('transform', `translate(${(W / 2 - 280 - rangeW) / 3}, ${H - 230})`)

        $scatterG.selectAll('.catG')
            .data(['popG', 'incomeG', 'ownerG', 'bachelorG', 'headlineG', 'tooltipG', 'legendG'])
            .join('g')
            .attr('class', 'catG')
            .attr('id', d => d)
            .attr('transform', (d, i) => `translate(0, ${(i < 4) ? 55 * i : 0})`)
        const $popG = $scatterG.select('#popG');
        const popScale = d3.scaleLinear().domain([0, 30000]).range([0, rangeW]);
        createScale($popG, popScale, 'pop', "Population")

        const $incomeG = $scatterG.select('#incomeG');
        const incomeScale = d3.scaleLinear().domain([40000, 110000]).range([0, rangeW]);
        createScale($incomeG, incomeScale, 'income', "Median household income")

        const $ownerG = $scatterG.select('#ownerG');
        const ownerScale = d3.scaleLinear().domain([0.4, 1]).range([0, rangeW]);
        createScale($ownerG, ownerScale, 'owner', "Homeownership rate")

        const $bachelorG = $scatterG.select('#bachelorG');
        const bachelorScale = d3.scaleLinear().domain([0.1, 0.5]).range([0, rangeW]);
        createScale($bachelorG, bachelorScale, 'bachelor', "College-educated residents")

        const $headlineG = $scatterG.select('#headlineG')
            .attr('transform', `translate(0, -58)`);
        const $legendG = $scatterG.select('#legendG')
            .attr('transform', `translate(0, -24)`);
        $legendG.selectAll('text')
            .data(['◯ represents a town'])
            .join('text')
            .text(d => d)
            .attr('fill', '#686868')
            .attr('font-size', 11)


        $headlineG.selectAll('.headline')
            .data(['Demographics of', 'towns'])
            .join("text")
            .attr('transform', (d, i) => `translate(0, ${i * 16})`)
            .attr('class', 'headline')
            .attr('id', (d, i) => `${'hed_' + i}`)
            .text(d => d)
            .attr('fill', (d, i) => i == 0 ? '#333333' : _green)
            .attr('font-size', '14px')
            .attr('font-weight', 900)

        function createScale($g, scale, attr, title = '') {
            $g.selectAll('.rectBG')
                .data([1])
                .join('rect')
                .attr('class', 'rectBG')
                .attr('width', rangeW + 1)
                .attr('height', 10)
                .attr('x', 0)
                .attr('y', 0)
                .attr('fill', '#eeeeee')

            var yAxis = d3.axisBottom()
                .scale(scale)
                .ticks(4)
                .tickSize(10)
                .tickFormat(d => (d <= 1) ? (d == 0 ? 0 : d * 100 + '%') : (d === 40000 ? '$40k' : (d > 1000 ? d / 1000 + 'k' : d)))

            const axis = $g.selectAll('.axisG')
                .data([1])
                .join('g')
                .attr('class', 'axisG')
                .attr('transform', `translate(0, 0)`)
                .attr("class", "y axis")
                .attr('fill', _gray)
                .call(yAxis);



            const circle = $g.selectAll('.town')
                .data(townInfo)
                .join('circle')
                .attr('class', d => `town ${slugify(d.town)}`)
                .attr('cx', d => scale(d[attr]))
                .attr('cy', 5)
                .attr('r', 3)
                .attr('stroke', _gray)
                .attr('fill', _gray)
                .attr('fill-opacity', 0.3);

            circle.on('mouseover', function (d) {
                console.log('mouseover!!!')
                const id = d3.select(this).attr('class')
                const _id = id.replace('town ', '')
                $scatterG.selectAll(`.${_id}`)
                    .attr('fill-opacity', 1)
                    .attr('r', 5).attr('fill', _green).raise();

                const posX = $popG.select(`.${_id}`).attr('cx')
                const posY = $popG.select(`.${_id}`).attr('cy')

            })
                .on('mouseout', function () {
                    $scatterG.selectAll(`.town`).attr('fill', _gray)
                        .attr('r', 3)
                        .attr('fill-opacity', 0.3);
                })




            axis
                .append("text")
                .attr('class', 'scatter__title')
                .attr("transform", `translate(0, 0)`)
                .attr("y", -7)
                .attr("dy", "0")
                .style("text-anchor", "start")
                .attr('fill', '#333333')
                .attr('font-weight', '600')
                .attr('font-size', '12px')
                .text(title);

            axis.selectAll('.domain').remove()

        }

    }

    init.active = function () {
        const isActive = `${(W >= 960) ? 'active' : ''}`;
        if (isActive != '') {
            $svg.selectAll('.scatterG').classed('active', true)
        }
    }

    init.highlightTown = function (arr) {
        const $scatterG = $svg.selectAll('.scatterG')
        $scatterG.selectAll(`.town`).attr('fill', _gray)
            .attr('r', 3)
            .attr('fill-opacity', 0.3);

        if (arr[0] == 'West Easton') {
            arr.shift()
        }

        arr.forEach((d, i) => {
            console.log(slugify(d));
            $scatterG.selectAll(`.${slugify(d)}`)
                .attr('fill-opacity', 1)
                .attr('r', 5).attr('fill', i == 0 ? _green : _green2).raise();
        })

        const hed_1 = $svg.select('#headlineG')
            .select('#hed_1');
        if (arr.length > 1) {
            hed_1.select('*').remove()
            hed_1.text(arr[0] + ', ')
            hed_1.append('tspan')
                .text(arr[1])
                .attr('fill', _green2)
        } else {
            hed_1.select('*').remove()
            hed_1.text(arr[0]).attr('fill', _green)
        }

    }


    return init
}

export default ScatterPlot;