function getCottingham() {
    const cottingham = [[40.691921, -75.226414], [40.690959, -75.226328], [40.691040, -75.224440], [40.692040, -75.224554], [40.691921, -75.226414]]
    const cData = cottingham.map(d=>[d[1], d[0]])
    return parseGeoData(cData);
}

function parseGeoData(arr) {
    return {
        'type': 'Feature',
        'geometry': {
            'type': 'LineString',
            'coordinates': arr
        }
    }
}

export default { getCottingham, }