export default function lazyLoadVideo() {
    const allVideoElem = ['overview__video', 'palmer__video', 'lehigh__video', 'ending__video'];
    allVideoElem.forEach(videoEl => {
        const $video = d3.select(`#${videoEl}`);
        const dataSrc = $video.attr('data-src');
        $video.append('source').attr('src', dataSrc);
        document.getElementById(`${videoEl}`).load(); // this line somehow fixed my mobile (iphone6s, Safari) view
    });
}
